@font-face {
    font-family: "Helvetica";
    src:
        url(../fonts/helvetica_regular.woff) format("woff"),
        url(../fonts/helvetica_regular.woff2) format("woff2");
    font-display: swap;
    font-weight: 400;
    font-style: normal;
}
@font-face {
    font-family: "Helvetica";
    src:
        url(../fonts/helvetica_light.woff) format("woff"),
        url(../fonts/helvetica_light.woff) format("woff2");
    font-display: swap;
    font-weight: 300;
    font-style: normal;
}

@font-face {
    font-family: "Helvetica";
    src:
        url(../fonts/helvetica_bold.woff) format("woff"),
        url(../fonts/helvetica_bold.woff2) format("woff2");
    font-display: swap;
    font-weight: 700;
    font-style: normal;
}
