.slider {
    position: absolute;
    max-width: 525px;
    z-index: 0;
    // overflow: hidden;
    width: 100%;
    min-height: 554px; /* Регулируйте высоту по вашему усмотрению */
    right: 0;
    bottom: 0;
    img {
        object-fit: contain;
    }

    &:hover {
        img {
            filter: drop-shadow(14px 1px 40px $red);
        }
    }
    @media screen and (max-width: 768px) {
        position: relative;
        max-width: 100%;
        text-align: center;
        img {
            object-fit: cover;
        }
    }
    @media screen and (max-width: 500px) {
        min-height: 400px;
    }
}

.slider-item {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
}

.slider-item.active {
    opacity: 1;
}

.promo {
    margin-top: 115px;
    &__container {
        display: flex;
        // justify-content: center;
        align-items: center;
        min-height: 554px;
        height: auto;
        position: relative;
        z-index: 1;
        @media screen and (max-width: 768px) {
            flex-direction: column;

            gap: 30px 0;
        }
    }

    &__image {
        transition: 0.3s ease-in-out all;
    }

    // &:active {

    //                 }

    &__info {
        display: flex;
        flex-direction: column;
        max-width: 640px;
        width: 100%;
        position: relative;
        z-index: 2;
    }

    &__title {
        font-size: 50px;
        color: $white;
        font-weight: 700;

        margin-bottom: 20px;
    }
    &__desr {
        font-size: 20px;
        color: $white;
        br {
            display: none;
        }
        margin-bottom: 10px;
    }
    &__text {
        color: #8d8d8d;
        font-size: 20px;

        margin-bottom: 50px;
    }

    &__buttons {
        display: flex;
        gap: 0 20px;
    }
    &__typed {
        color: $red;
        text-transform: uppercase;
    }
    &__button {
        min-width: 240px;
        justify-content: center;
    }
    @media screen and(max-width:767px) {
        &__container {
            align-items: flex-start;
            justify-content: center;
            padding-top: 70px;
            // padding-bottom: 70px;
            min-height: auto;
        }
        &__info {
            align-items: center;
        }
        &__title {
            font-size: 32px;
            br {
                display: none;
            }
        }
        &__desr {
            br {
                display: block;
            }
        }
        &__title,
        &__text,
        &__desr {
            text-align: center;
        }
        &__buttons {
            flex-direction: column;
            gap: 20px 0;
            width: 100%;
        }
        &__button {
            padding: 15px 17px;

            span {
                font-size: 18px;
            }
            &.border {
                padding: 13px 15px;
            }
            display: inline-flex;
            align-items: center;
            justify-content: center;
            max-width: 100%;
            min-width: 240px !important;
        }
    }
    @media screen and(max-width:500px) {
        &__container {
            padding-top: 50px;
            // padding-bottom: 50px;
        }
        &__text,
        &__desr {
            font-size: 16px;
        }
        &__title {
            font-size: 24px;
        }
    }
}
